@import url(https://fonts.googleapis.com/css2?family=Indie+Flower);
body {
  margin: 0;
  font-family: 'Indie Flower';
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eceff1;
  color: #4d4e4d;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Pokecard{
    /* border: 2px red solid; */
    width: 300px;
    padding: 10px;
    margin: 1rem;
    box-shadow:  7px 10px 12px -5px rgba(0,0,0, 0.6);
    text-align: center;
    background-color: white;
    border-radius: 3px;
}

.Pokecard-Data{
    font-size: 80%;
}

.Pokecard-title{
    margin-bottom: 1rem;
    font-weight: bold;
    font-size: 1.5rem;
}

.Pokecard-img{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 250px;
    background-color: #eceff1;
    border-radius: 3rem;
    margin-bottom: 1rem;
}

.Pokecard img{
    transition: all ease 500ms;
}

.Pokecard img:hover{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 0.8;
}
.Pokedex{
    text-align: center;
}
.Pokedex-Cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.Pokedex-Winner{
    color: #4caf50;
}

.Pokedex-Loser{
    color: #e91e63 ;
}

